<template>
  <div id="login">
     <el-form class="login-form" status-icon :rules="loginRules" ref="loginForm" :model="loginForm" label-width="0">
      <el-form-item prop="phone">
        <el-input size="small" @keyup.enter.native="handleLogin" v-model="loginForm.phone" auto-complete="off" placeholder="请输入用户名">
          <i slot="prefix" class="icon-yonghu"></i>
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input size="small" @keyup.enter.native="handleLogin" :type="passwordType" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码">
          <i class="el-icon-view el-input__icon" slot="suffix" @click="showPassword"></i>
          <i slot="prefix" class="icon-mima"></i>
        </el-input>
      </el-form-item>
      <!-- <el-checkbox v-model="checked">记住账号</el-checkbox> -->
      <el-form-item>
        <!-- <el-button type="primary" size="small" @click.native.prevent="handleLogin" class="login-submit">登录1</el-button> -->
        <el-button type="primary" size="small" @click="handleLogin" class="login-submit">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      loginForm: {
        phone: '15397133767',
        password: '123456'
      },
      passwordType: 'password',
      loginRules: {
        phone: [
          {required: true, validator: this.noEmpty, message: '请输入用户名', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    showPassword() {
      this.passwordType === ''
        ? (this.passwordType = 'password')
        : (this.passwordType = '')
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.httpPostJson(this._login, this.loginForm).then(res => {
            if (res.success) {
              this.$router.push({path: '/home'})
            }
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#login{
  width:100%;
  height:100%;
  display:flex;
  justify-content: center;
  align-items: center;
  background: url(/static/img/jinmen.jpg) center center no-repeat;
  background-size: 100% 100%
}
#login .el-form{
  background: rgba(200, 200, 200, 0.5);
  border-radius: 10px;
  padding:30px;
}
#login .el-checkbox{
  text-align:left;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



