<template>
  <div style="width:100%;height:100%">
    <div id="map" style="width:100%;height:100%"></div>
    <div id="infoForm" v-show="showForm">
      <el-form :model="curItem" label-width="70px">
        <el-form-item label="名称: ">
          <el-input v-model="curItem.name"></el-input>
        </el-form-item>
        <el-form-item label="联系人: ">
          <el-input v-model="curItem.linkman"></el-input>
        </el-form-item>
        <el-form-item label="联系电话: ">
          <el-input v-model="curItem.phone"></el-input>
        </el-form-item>
        <el-form-item label="土地面积: ">
          <el-input v-model="curItem.area"></el-input>
        </el-form-item>
        <el-form-item label="地块价格: ">
          <el-input v-model="curItem.price"></el-input>
        </el-form-item>
        <!-- <el-form-item label="详细地址: ">
          <el-input v-model="curItem.address"></el-input>
        </el-form-item> -->
        <el-form-item label="备注: ">
          <el-input type="textarea" v-model="curItem.remark"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button type="primary" size="mini" @click="saveData">保存</el-button>
        <el-button type="primary" size="mini" v-if="curItem.id !== undefined && curItem.id !== null" @click="delData">删除</el-button>
      </div>
      <!-- <div style="text-align:center; margin: 10px"><input type='button' value='保存' @click="saveData"/></div>
      <div style="text-align:center; margin: 10px"><input type='button' value='删除' @click="删除"/></div> -->
    </div>
    <!-- <el-radio-group v-model="type" @change="switchType" style="position: absolute; right: 100px; top: 10px">
      <el-radio-button label="satellite">卫星</el-radio-button>
      <el-radio-button label="roadmap">地图</el-radio-button>
      <el-radio-button label="hybrid">混合</el-radio-button>
      <el-radio-button label="terrain">地形</el-radio-button>
    </el-radio-group> -->
    <!-- <el-button type="info" style="position: absolute; right: 100px; top: 10px" @click="logout">退出</el-button> -->
  </div>
</template>
<script>
import { arrs } from './const'
import axios from './common'
var map, editForm
export default {
  name: '',
  data() {
    return {
      showForm: false,
      curItem: {
        name: '',
        address: '',
        area: '',
        linkman: '',
        phone: '',
        price: '',
        remark: '',
        coordinates: []
      },
      type: 'type',
      polygonObj: null
    }
  },
  mounted() {
    window.google ? this.initMap() : this.init()
  },
  methods: {
    init() {
      let self = this
      window.onload = function () {
        self.initMap()
      }
    },
    saveData() {
      let param = this.curItem
      param.coordinates = 
      param = {
        id: this.curItem.id,
        name: this.curItem.name,
        address: this.curItem.address,
        area: this.curItem.area,
        linkman: this.curItem.linkman,
        phone: this.curItem.phone,
        price: this.curItem.price,
        lat: this.curItem.lat,
        lng: this.curItem.lng,
        remark: this.curItem.remark,
        coordinates: this.calcLines(this.curItem.polygon)
      }

      this.httpPost(this._save, param).then(res => {
        this.$message.success('保存成功')
        editForm.close();
        if (this.curItem.id !== null && this.curItem.id !== undefined) {
          this.curItem.marker.setMap(null)
          this.curItem.polygon.setMap(null)
        }
        this.getDatas()
      })
    },
    delData() {
      this.httpPostJson(this._delData, {id: this.curItem.id}).then(res => {
        if (res.success) {
          this.curItem.marker.setMap(null)
          this.curItem.polygon.setMap(null)
          this.$message.success('删除成功')
        }
      })
    },
    switchType(type) {
      map.setMapTypeId(type)
    },
    calcLines(polygon) {
      let arr = []
      if (polygon) {
        var len = polygon.getPath().getLength();
        for (var i = 0; i < len; i++) {
          const val = polygon.getPath().getAt(i).toUrlValue(6).split(',')
          arr.push({lat: val[0], lng: val[1]})
        }
      }
      return arr
    },
    getAll() {
      return new Promise((resolve, reject) => {
        this.httpPost(this._byPage).then(res => {
          resolve(res)
        })
      })
    },
    newPolygon(coordinates) {
      let polygon = new google.maps.Polygon({
        paths: coordinates,
        strokeColor: '#FF0000',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        editable: true,
        fillColor: '#FF0000',
        fillOpacity: 0.35
      })
      polygon.setMap(map)
      return polygon
    },
    newMarker(lat, lng) {
      return new google.maps.Marker({
        position: {lat: lat, lng: lng},
        map: map
      })
    },
    constructArr(arrs) {
      let self = this
      for (let item of arrs) {
        if (item.coordinates && item.coordinates.length && item.lat && item.lng) {
          item.coordinates.forEach(n => {
            n.lat = n.lat - 0
            n.lng = n.lng - 0
          })
          let polygon = this.newPolygon(item.coordinates)
          let marker = this.newMarker(item.lat, item.lng)
          marker.polygon = polygon
          marker.addListener('click', function(e) {
            self.showForm = true
            self.curItem = item
            self.curItem.marker = marker
            self.curItem.polygon = polygon
            editForm.open(map, marker);
          })
        }
      }
    },
    async getDatas() {
      // 台湾省金门县金湖镇福利中心
      let result = await this.getAll()
      if (result.success && result.data && result.data.length) {
        this.constructArr(result.data)
      }
    },
    initMap() {
      let self = this
      // 初始化地图
      map = new google.maps.Map(document.getElementById('map'), {
        center: { lat: 24.47, lng: 118.33 },
        zoom: 12,
        // MapTypeId.ROADMAP，用于显示默认的道路地图视图
        // MapTypeId.SATELLITE，用于显示 Google 地球卫星图片
        // MapTypeId.HYBRID，用于同时显示普通视图和卫星视图
        // MapTypeId.TERRAIN，用于根据地形信息显示实际地图。
        mapTypeId: google.maps.MapTypeId.HYBRID
      });
      // 获取中心
      google.maps.Polygon.prototype.getBoundingBox = function() {
        var bounds = new google.maps.LatLngBounds();
        this.getPath().forEach(function(element, index) {
            bounds.extend(element)
        });
        return bounds;
      };
      // Add center calculation method
      google.maps.Polygon.prototype.getApproximateCenter = function() {
        var boundsHeight = 0,
          boundsWidth = 0,
          centerPoint,
          heightIncr = 0,
          maxSearchLoops,
          maxSearchSteps = 10,
          n = 1,
          northWest,
          polygonBounds = this.getBoundingBox(),
          testPos,
          widthIncr = 0;

        // Get polygon Centroid
        centerPoint = polygonBounds.getCenter();

        if (google.maps.geometry.poly.containsLocation(centerPoint, this)) {
            // Nothing to do Centroid is in polygon use it as is
            return centerPoint;
        } else {
          maxSearchLoops = maxSearchSteps / 2;
          
          // Calculate NorthWest point so we can work out height of polygon NW->SE
          northWest = new google.maps.LatLng(polygonBounds.getNorthEast().lat(), polygonBounds.getSouthWest().lng());

          // Work out how tall and wide the bounds are and what our search increment will be
          boundsHeight = google.maps.geometry.spherical.computeDistanceBetween(northWest, polygonBounds.getSouthWest());
          heightIncr = boundsHeight / maxSearchSteps;
          boundsWidth = google.maps.geometry.spherical.computeDistanceBetween(northWest, polygonBounds.getNorthEast());
          widthIncr = boundsWidth / maxSearchSteps;

          // Expand out from Centroid and find a point within polygon at 0, 90, 180, 270 degrees
          for (; n <= maxSearchLoops; n++) {
              // Test point North of Centroid
              testPos = google.maps.geometry.spherical.computeOffset(centerPoint, (heightIncr * n), 0);
              if (google.maps.geometry.poly.containsLocation(testPos, this)) {
                  break;
              }

              // Test point East of Centroid
              testPos = google.maps.geometry.spherical.computeOffset(centerPoint, (widthIncr * n), 90);
              if (google.maps.geometry.poly.containsLocation(testPos, this)) {
                  break;
              }

              // Test point South of Centroid
              testPos = google.maps.geometry.spherical.computeOffset(centerPoint, (heightIncr * n), 180);
              if (google.maps.geometry.poly.containsLocation(testPos, this)) {
                  break;
              }

              // Test point West of Centroid
              testPos = google.maps.geometry.spherical.computeOffset(centerPoint, (widthIncr * n), 270);
              if (google.maps.geometry.poly.containsLocation(testPos, this)) {
                  break;
              }
          }

          return testPos;
        }
      };

      // 编辑弹框
      editForm = new google.maps.InfoWindow({
        content: document.getElementById('infoForm')
      })

      // 初始化绘图工具
      var drawingManager = new google.maps.drawing.DrawingManager({
        drawingMode: null, // google.maps.drawing.OverlayType.MARKER
        drawingControl: true,
        editable: true,
        OverlayCompleteEvent: null,
        drawingControlOptions: {
          position: google.maps.ControlPosition.BOTTOM_CENTER,
          drawingModes: ['polygon'] // , 'circle', 'polygon', 'polyline', 'rectangle'
        },
        // markerOptions: {icon: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png'},
        polygonOptions: {
          fillColor: '#000',
          fillOpacity: 0.1,
          strokeWeight: 5,
          clickable: false,
          editable: true,
          zIndex: 1
        }
      });
      drawingManager.setMap(map);
      
      // 多边形绘图完成触发事件
      google.maps.event.addListener(drawingManager, 'polygoncomplete', function(polygon) {
        let marker = new google.maps.Marker({
          position: polygon.getApproximateCenter(),
          map: map
        });
        google.maps.event.addListener(marker, 'click', function() {
          self.showForm = true
          self.curItem = {
            name: '',
            address: '',
            area: '',
            linkman: '',
            phone: '',
            price: '',
            remark: '',
            coordinates: []
          }
          self.curItem.polygon = polygon
          self.curItem.marker = marker
          self.curItem.lat = marker.getPosition().lat()
          self.curItem.lng = marker.getPosition().lng()
          editForm.open(map, marker);
          // console.error(marker.getPosition().lat())
          // console.error(marker.getPosition().lng())
        });
        drawingManager.setOptions({drawingMode: null})
      });
      
      this.getDatas()
    }
  }
}
</script>
<style>
  .el-form-item{
    margin-bottom: 5px;
  }
  .el-form-item .el-form-item__label, .el-form-item .el-form-item__content{
    font-size:30px;
    color:#000;
    font-size:12px;
    line-height:20px;
  }
  .el-form-item .el-input, .el-textarea{
    font-size: 12px
  }
</style>



